// Suppose you have an array of 101 integers. This array is already sorted and all numbers in this array are consecutive. Each number only occurs once in the array except one number which occurs twice. Write a js code to find the repeated number.
// e.g $arr = array(0,1,2,3,4,5,6,7,7,8,9,10...................);


import React from 'react'

function Question_3() {

    const arr=[0,1,2,3,4,5,6,7,7,8,9,10,11,12,13];
    let repeatNum;

    //note: The array is already sorted and have consecutive numbers given

for (let i = 0; i < arr.length; i++) {
  if (arr[i + 1] === arr[i]) {
    repeatNum=arr[i];
  }
}

console.log(repeatNum);

    return (
        <div>
            <p>The repeated Number is {repeatNum}</p>
        </div>
    )
}

export default Question_3
